// Fit height
html.epub-titlepage{
  -webkit-writing-mode: horizontal-tb;
  writing-mode: horizontal-tb;
}

.titlepage{
  &__title{
    text-align: center;
    margin-top: 0;
    padding-top: 3em;
    span{
      display: block;
    }
    small{
      display: block;
      font-size: 0.6em;
      margin-top: 1em;
      &.subtitle{
        font-size: 0.8em;
        margin-top: 0;
      }
      &.publisher-credit{
        color: #999;
      }
    }
  }
}
